body{
    background-color: #fff;
}
.top-logo-wrap{
    .top-logo{
        input{
                border: 2px solid #30b30e;
        }
        button{
            background-color: #30b30e;
        }
    }
}
.line{
    width: 100%;
    height: 1px;
    background-color: #000;
}
.detailcenter{
    @include public(600px);
    margin-top: 50px;
    .detail-left{
        width: 400px;
        height: 580px;
        float: left;
        padding: 10px 50px;
        .small-box{
            position: relative;
            width: 400px;
            height: 400px;
            img{
                width: 400px;
                height: 400px;
            }
            .iconfont{
                position: absolute;
                bottom: 0;
                right: 0;
                width: 30px;
                height: 30px;
                background-color: #eee;
                text-align: center;
                line-height: 30px;
                opacity: 0.8;
                cursor: pointer;
            }
        }
        .small-pic{
            background-color: yellow;
            opacity: 0.6;
            position: absolute;
            display: none;
        }
        .scrollImg{
            width: 385px;
            height: 70px;
            margin: 0 auto;
            margin-top: 20px;
            li{
                float: left;
                img{
                    width: 60px;
                    height: 60px;
                    margin-left: 15px;
                }
            li:first-child{
                margin-left: 0;
            }   
            }
        }
        .big-box{
            position: absolute;
            width: 500px;
            height: 500px;
            overflow: hidden;
            top: 225px;
            left: 605px;
            opacity: 0;
            background-color: white;
            z-index: -1;
            img{
                width: 800px;
                height: 800px;
                position: absolute;
            }
        }
        .person{
            width: 400px;
            height: 95px;
            .person-left{
                height: 95px;
                width: 200px;
                float: left;
                line-height: 95px;
                font-size: 12px;
                color: #999;
                b{
                    font-weight: normal;
                    margin-right: 5px;
                }
                em{
                    font-style: normal;
                }
            }
            .person-right{
                height: 95px;
                width:100px;
                float: right;
                text-align: center;
                line-height: 95px;
                font-size: 12px;
                color: #999;
            }
        }
    }
    .detail-center{
        width: 500px;
        height: 580px;
        float: left;
        padding: 10px 0;
        // background-color: #ccc;
        .title{
            font-weight: bold;
            font-size: 16px;
            line-height: 18px;
        }
        .detail{
            font-size: 16px;
            line-height: 18px;
            color: red;
            margin-top: 5px;
            margin-bottom: 20px;
        }
        .price{
            height: 40px;
            width: 500px;
            background-color: #eee;
            position: relative;
            span{
                display: inline-block;
                width: 70px;
                line-height: 40px;
                font-size: 13px;
                color: #999;
                text-indent: 10px;
            }
            b{
                position: relative;
                display: inline-block;
                color: red;
                top: 4px;
                em{
                    font-style: normal;
                    font-size: 16px;
                }
                font-size: 24px;
            }
        }
        .weight{
            height: 30px;
            width: 500px;
            margin-top: 20px;
            span{
                height: 30px;
                display: inline-block;
                width: 70px;
                line-height: 30px;
                font-size: 13px;
                color: #999;
                text-indent: 10px;
            }
            b{
                display: inline-block;
                color: #999;
                em{
                    font-style: normal;
                    font-size: 13px;
                    font-weight: normal;
                    margin-left: 10px;
                }
                font-size: 13px;
            }
        }
        .score{
            margin-top: 20px;
            height: 40px;
            width: 500px;
            border-top: 1px dotted #c9c9c9;
            border-bottom: 1px dotted #c9c9c9;
            ul{
                height: 20px;
                margin-top: 10px;
                .comment{
                    width: 249px;
                    border-right: 1px solid #c9c9c9;
                    float: left;
                    font-size: 12px;
                    line-height: 20px;
                    color: #666;
                    text-indent: 70px;
                    span{
                        color: red;
                        margin-left: 2px;
                        font-weight: bold;
                    }
                }
                .getscore{
                    text-indent: 70px;
                    width: 250px;
                    float: right;
                    color: #666;
                    font-size: 12px;
                    line-height: 20px;
                    span{
                        color: #280;
                        margin-left: 5px;
                        font-weight: bold;
                    }
                }
            }
            
        }
        .shopnum{
            margin-top: 20px;
            height: 36px;
            width: 500px;
            margin-bottom: 30px;
            .num-left{
                width: 70px;
                line-height: 36px;
                font-size: 13px;
                color: #999;
                text-indent: 10px;
                float: left;
            }
            input{
                width: 40px;
                height: 32px;
                border: 1px solid #ccc;
                float: left;
                margin-top: 5px;
                text-indent: 3px;
            }
            ul{
                height: 30px;
                width: 20px;
                float: left;
                margin-top: 3px;
                cursor: pointer;
                margin-left: 5px;
                li{
                    height: 14px;
                    border: 1px solid #ccc;
                    width: 18px;
                    margin-top: 2px;
                    position: relative;
                    user-select: none;
                    .iconfont{
                        font-size: 1px;
                        text-align: center;
                        position: relative;
                        left: 2px;
                        bottom: 6px;
                        color: gray;
                        &:hover{
                            color: #000;
                        }
                    }
                }
            }
            .letter{
                color: #838383;
                font-size: 13px;
                margin-left: 10px;
                line-height: 40px;
            }
        }
        .addcar{
            display: inline-block;
            width: 180px;
            height: 40px;
            font-size: 16px;
            color: #fff;
            line-height: 40px;
            text-align: center;
            position: relative;
            background-color: #ff0036;
            cursor: pointer;
            left: 30px;
            user-select: none;
            text-align: center;
        }
        .delivery{
            margin-top: 50px;
            .address{
                color: #865;
                font-size: 13px;
                padding-left: 10px;
                margin-top: 10px;
                span{
                    display: inline-block;
                    width: 70px;
                    height: 13px;
                    font-size: 13px;
                    color: #999;
                }
            }
            .notice{
                color: #999;
                font-size: 13px;
                padding-left: 10px;
                margin-top: 10px;
            }
        }
    }
    .detail-right{
        float: left;
        width: 200px;
        height: 600px;
        margin-left: 15px;
        border-left: 1px solid #ddd;
        p{
            position: relative;
            height: 20px;
            line-height: 20px;
            color: #999;
            font-size: 13px;
            text-align: center;
            span{
                display: inline-block;
                height: 1px;
                background-color: #999;
                width: 30px;
                position: relative;
                top: -3px;
            }
        }
        .instruduce{
            position: relative;
            width: 180px;
            height: 460px;
            margin-left: 30px;
            overflow: hidden;
            img{
                height: 140px;
                width: 140px;
                margin: 5px 0;
            }
            .imgwrap{
                position: absolute;
            }
        }
        .bn{
            margin-top: 10px;
            width: 180px;
            height: 30px;
            margin-left: 30px;
            user-select: none;
            .bn-left{
                display: block;
                width: 90px;
                line-height: 30px;
                text-indent: 40px;
                float: left;
                color: #999;
                em{
                    &:hover{
                        color: red;
                        
                    }
                    cursor: pointer;
                }
                
            }
            .bn-right{
                width: 90px;
                line-height: 30px;
                display: block;
                float: left;
                color: #999;
                em{
                    cursor: pointer;
                    &:hover{
                        color: red;
                        
                    }
                }
            }
        }
    }
}
    
.end{
    @include public(60px);
    background-color: #eee;
    margin-bottom: 20px;
    text-align: center;
    margin-top: 150px;
    img{
        height: 40px;
        margin-top: 10px;
        display: inline-block;
    }
}