.page-bottom{
    .page-bottom-1{
        width: 100%;
        height: 313px;
        background-color: #fff;
        .page-bottom-top{
            @include public(100px);
            border-bottom: 1px solid gray;
            ul{
                li{
                    width: 307px;
                    float: left;
                    p{
                        line-height: 30px;
                        font-size: 18px;
                        text-align: center;
                    }
                    p:nth-child(1){
                        font-weight: bold;
                        margin-top: 20px;
                    }
                    p:nth-child(2){
                        color: gray;
                    }
                }
            }
        }
        .page-bottom-last{
            @include public(213px);
            ul{
                li{
                    width: 246px;
                    float: left;
                    h1{
                        font-size: 18px;
                        height: 30px;
                        line-height: 30px;
                        margin-top: 20px;
                        color: rgb(65, 62, 62);
                        width: 200px;
                        margin-left: 20px;
                    }
                    p{
                    font-size: 14px; 
                    color: gray;
                    width: 200px;
                    height: 20px;
                    margin-left: 20px;
                    }
                    img{
                        width: 105px;
                        margin-left: 10px;
                    }
                }
            }
        }
        }
    .page-bottom-2{
        width: 100%;
        height: 220px;
        background-color: #000;
        color: white;
        .page-bottom-2main{
            @include public(220px);
            %t{
                font-size: 12px;
                line-height: 30px;
                height: 30px;
                color: white;
            }
            .t1{
                @extend %t;
            }
            .t2{
                @extend %t;
            }
            .t3{
                @extend %t;
                color: gray;
            }
            p{
                height: 20px;
                line-height: 20px;
                font-size: 12px;
                color: gray;
                span{
                    margin-right: 50px;
                }
            }
            .t4 img{
                height: 30px;
                margin-top: 10px;
                margin-right: 30px;
            }
        }
    }
}
