@mixin lineset($color,$font,$lineh){
    color: $color;
    font-size: $font;
    line-height: $lineh;
    text-align: center;
}
@mixin  public($h){
    width: 1230px;
    margin: 0 auto;
    height: $h;
}
@mixin clearfix {
    content: '';
    clear: both;
    display: block;
    overflow: hidden;
}
body{
    @extend html;
    background-color: #f5f5f5;
}
/*!悬浮搜索框*/

.topwrap{
    @extend html;
    height: 34px;
    background-color: #eee;
    .top{
        @include public(34px);
        .top-left{
            float: left;
            width: 280px;
            height: 34px;
            @include lineset(gray, 13px,34px);
            .log,.sign{
                cursor: pointer;
                color: gray;
                margin-left: 30px;
                &:hover{
                    color: red;
                }
            }
        }
        .top-right{
            position: relative;
            float: right;
            height: 34px;
            b{
                display: inline-block;
                width: 1px;
                height: 20px;
                margin-top: 6px;
                background-color: gray;
                position: absolute;
            }
            li{
                float: left;
                cursor: pointer;
                &:hover{
                    color: red;
                }
                width: 74px;
                @include lineset(gray,13px,34px);
            }
            li span{
                margin-top: 2px;
                margin-right: 5px;
                color: red;
                font-size: 13px;
            }
            li:nth-child(3){
                margin-right: 5px;
            }
            li:nth-child(5){
                margin-left: 5px;
            }
            %mytaobao{
                width: 100px;
                height: 50px;
                position: absolute;
                top: 34px;
                background-color: white;
                border: 1px solid #ccc;
                border-top: none;
                cursor: pointer;
                p{
                    @include lineset(gray,12px,15px);
                    height: 15px;
                    margin-top: 8px;
                    &:hover{
                        color: red;
                        text-decoration: underline;
                    }
                }
            }
            .mytaobao{
                @extend %mytaobao;
                display: none;
            }
            .collect{
                @extend %mytaobao;
                display: none;
                left: 148px;
            }
            .img{
                display: none;
                position: absolute;
                left: 200px;
                top: 26px;
                img{
                    width: 150px;
                   
                }
            }
            .merchant{
                position: absolute;
                top: 34px;
                width: 160px;
                height: 180px;
                border: 1px solid #ccc;
                border-top: none;
                overflow: hidden;
                left: 292px;
                display: none;
                background-color: white;
                z-index:1;
                p{
                    text-indent: 10px;
                    font-size: 13px;
                    height: 15px;
                }
                em{
                    width: 140px;
                    display: block;
                    height: 1px;
                    border-bottom: 1px dashed #000;
                    margin: 5px 0;
                    margin-left: 10px;
                }
                li{
                    margin: 0;
                    height: 22px;
                    &:hover{
                        text-decoration: underline;
                    }
                }
                ol{
                    height: 100px;
                    width: 160px;
                }
            }
            .webnav{
                position: absolute;
                top: 34px;
                width: 1200px;
                height: 150px;
                left: -673px;
                border: 1px solid #ccc;
                border-top: none;
                display: none;
                z-index:1;
                background-color: #fff;
                li{
                    margin: 0;
                    &:hover{
                        text-decoration: underline;
                    }
                }
                p{
                    height: 50px;
                    line-height: 50px;
                    text-indent: 15px;
                }
                .left{
                    float: left;
                    width: 400px;
                    padding: 0 20px;
                    p{
                        color: orange;
                    }
                }
                .center{
                    float: left;
                    width: 400px;
                    padding: 0 20px;
                    p{
                        color: blue;
                    }
                }
                .right{
                    float: left;
                    width: 200px;
                    padding: 0 10px;
                }
            }
        }
    }
}
.top-logo-wrap{
    width:100%;
    height: 130px;
    background-color: white;
    .top-logo{
        @include public(130px);
        img{
            width: 240px;
            float: left;
        }
        input{
            display: block;
                width: 500px;
                height: 40px;
                float: left;
                margin-top: 35px;
                margin-left: 50px;
                font-size: 14px;
                text-indent: 10px;
                color: black;
                outline: none;
                border: 2px solid red;
                border-right: none;
        }
        button{
            width: 130px;
            height: 44px;
            border: none;
            background-color: red;
            float: left;
            @include lineset(white,20px,32px );
            margin-top: 8px;
            outline: none;
            margin-top: 35px;
        }
    }
}
